import * as React from 'react';
import { Button } from '@ali/we-design';
import { Controller } from '@ali/4ever-cangjie';
import { PluginRoles } from '@ali/4ever-bamboo';
import { setHeading } from '../actions';

export interface HeadingButtonProps {
  controller: Controller;
}

export default function mobileHeadingButton(heading) {
  return function HeadingButton(props: HeadingButtonProps) {
    const { controller } = props;
    const { value } = controller;
    const { isCollapsedAtEdge } = controller.value.selection;
    const isDisabled = isCollapsedAtEdge
      || controller.query('isSelectionInImage')
      || controller.query('isSelectionInHr')
      || controller.query('isSelectionInLinkCard');

    const handleClick = () => controller.run('onAction', setHeading(heading));

    let level = '';
    const levelMatch = heading?.match(/\d+/);
    if (levelMatch && levelMatch[0]) {
      level = levelMatch[0];
    }

    return (
      <Button
        testid="bi-toolbar-heading"
        icon={`H${level}`}
        bordered={false}
        disabled={isDisabled}
        onClick={handleClick}
        role={PluginRoles.heading}
      />
    );
  };
}
